.actionOwner {
  width: 100vw;
  height: 100vh;

  .sup-navbar-center {
    font-size: 1rem;
  }

  .actionOwner-content {
    width: 100%;
    height: 92%;
    overflow-y: scroll;

    .actionOwner-accordion {

      .am-accordion-header {
        background: #f5f5f5;
      }

      .actionOwner-list {

        .list-content {
          .topBg {
            height: 1vh;
            background: #e5e5e5;
          }

          .content {
            padding: 1vh 0 1vh 3vw;
            font-size: 0.8rem;
            display: flex;
            align-items: center;

            span {
              color: #333;
            }

            .img {
              width: 3vw;
              height: 2vh;
              margin-right: 1vw;
            }

            .value {
              color: #000;
              font-weight: 600;
              padding-left: 2vw;
            }
          }
        }
      }
    }
  }
}